body {
  margin: 0;
  padding: 0;
  font-family: Proxima Nova, sans-serif;
  font-size: 10px;
  line-height: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

.card__container {

  /*width:100px;*/
  /*height: 100px;*/
  /*width: 25%;*/
  /*height: 25%;*/
  /*padding: 10px;*/
  /*margin: 5px;*/
  /*width:100px;*/
  /*height: 100px;*/
  background-color: red;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;

  -webkit-transform-style: preserve-3d;

          transform-style: preserve-3d;
  -webkit-transform-origin: center right;
      -ms-transform-origin: center right;
          transform-origin: center right;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.card__image {
  /*background-image: url("../public/static/card.png");*/
  /*background: no-repeat 50%;*/
  width: 100%;
  height: 100%;
  background-position: center top;
  background-size: 100% auto;
}

.card {
  position: relative;
  width: 100%;
  height: 100%;
  /*margin: 2px;*/
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.card.is-flipped {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.is-guessed {
  -webkit-animation: is-guessed-animation 2s forwards;
          animation: is-guessed-animation 2s forwards;
  /*transition: transform 1s ease-out 1s, opacity 1s ease-out 1s;*/
  /*animation-delay: 1s;*/
  /*transform: scale(.1);*/
  /*opacity: 0;*/
}

@-webkit-keyframes is-guessed-animation {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(.1);
            transform: scale(.1);
  }
}

@keyframes is-guessed-animation {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(.1);
            transform: scale(.1);
  }
}

.card__wrapper {

}

.card__scene {
  -webkit-perspective: 1000px;
          perspective: 1000px;
  width: 98%;
  height: 98%;
}




